.dish-card {
  display: block;
  min-height: 75vh;
  width: 100%;
  background: #FFFFFF;
  -webkit-box-shadow: 0 10px 20px 0 rgba(0,0,0,0.20);
          box-shadow: 0 10px 20px 0 rgba(0,0,0,0.20);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.dish-card .dish-card-poster {
  width: 100%;
  height: 210px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 370px;
}

.dish-card-detail {
  padding: 0 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.dish-card-detail>* {
}


.dish-card h1.dish-name {
  text-align: center;
  font-size: 26px;
  color: #F77062;
  line-height: 32px;
  margin-top: 45px;
}

.dish-card-icon-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.dish-card-icon-inner {
  width: 44px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.dish-card-icon-inner>* {
  display: block;
}

.dish-card-icon-wrap .dish-comment-no {
  line-height: 20px;
  color: #D0D0D0;
}


.dish-card .price-tag {
  color: #878787;
  text-align: center;
  margin-top: 20px;
}

.dish-card .price-tag span.unit {
  color: #F77062;
}

.dish-card .dish-desc {
  font-size: 14px;
  color: #878787;
  text-align: center;
  line-height: 24px;
  position: relative;
}

.dish-desc-mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.00)), to(#FFFFFF));
  background-image: -webkit-linear-gradient(top, rgba(255,255,255,0.00) 0%, #FFFFFF 100%);
  background-image: -o-linear-gradient(top, rgba(255,255,255,0.00) 0%, #FFFFFF 100%);
  background-image: linear-gradient(-180deg, rgba(255,255,255,0.00) 0%, #FFFFFF 100%);
}



