.home{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  -ms-flex-pack: justify;
      justify-content: space-between;
  /*对齐内容：空间两者之间*/
}
.home .hero{
}
.home .app-logo{
  display: block;
  margin: 0 auto;
  margin-top: 74px;
}
.home h1.title{
  font-size: 33px;
  color: #fff;
  line-height: 32px;
  text-align: center;
  margin-top: 74px;
}
.home p.slogan{
  font-size: 16px;
  color: #fff;
  text-align: center;
  margin-top: 53px;
}
.home .actions{
  margin-bottom: 90px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  /*内容对齐：空间-环绕，均分剩余空间*/
}
.home .actions>a{
  display: block;
  background-color: #fff;
  line-height: 56px;
  width: 150px;
  text-align: center;
  color: red;
  border-radius: 2px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.20);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.20);
}



