.cart {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.cart-hero {
  height: 230px;
  padding-top: 50px;
}

.cart-hero h1 {
  text-align: center;
  color: white;
}


.cart-list-wrap {
  background-color: #fff;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.cart-item-list {
  background-color: #fff;
  margin-top: -40px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
          box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
  overflow: auto;
  height: 40vh;
}

.cart-checkout-button {
  background-image: -webkit-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image: -o-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image: linear-gradient(-45deg, #F77062 0%, #FE5196 100%);
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.20);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.20);
  border-radius: 2px;
  font-size: 14px;
  line-height: 56px;
  text-align: center;
  color: #FFFFFF;
}



