.user {

}

.user-info-wrap {
  background: #F9FAFB;
  margin-top: 80px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  padding-top: 0;
}


.user-avatar {
  -webkit-box-shadow: 0 10px 20px 0 rgba(0,0,0,0.30);
          box-shadow: 0 10px 20px 0 rgba(0,0,0,0.30);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  display: block;
  position: absolute;
  left: 50%;
  margin-left: -60px;
  top: -60px;
  background-position: center center;
  background-size: 180px;
}

.user-name {
  margin-top: 80px;
  text-align: center;
  font-size: 26px;
  color: #F77062;
  line-height: 32px;
}

.user-info-card {
  background: #FFFFFF;
  margin-top: 50px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
          box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
  font-size: 16px;
  color: #878787;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  padding-top: 0;
  text-align: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-bottom: 5px;
}

.user-info-card-title {
  line-height: 24px;
  margin-top: 18px;
  margin-bottom: 18px;
  font-size: 16px;
  color: #F77062;
}

.user-follow-btn {
  background-image: -webkit-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image: -o-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image: linear-gradient(-45deg, #F77062 0%, #FE5196 100%);
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.20);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.20);
  border-radius: 2px;
  font-size: 14px;
  line-height: 56px;
  text-align: center;
  color: #FFFFFF;
}



